.scoreboard {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 10px 20px;
  height: 60px;
  line-height: 40px;
  vertical-align: middle;
  background: #e5e5e5;
  border: 1px solid #dedede;
  clear: both;

}

.mines, .time {
  flex-grow: 1;
  max-width: calc(50% - 20px);
}

.mines {
  text-align: left;
}

.time {
  text-align: right;
}

.status {

  flex-grow: 1;
  /* display: inline-block; */
  max-width: 40px;
  width: 40px;
  min-width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background-color: #eee;
  border-left: 2px solid #f5f5f5;
  border-top: 2px solid #f5f5f5;
  border-right: 2px solid #dedede;
  border-bottom: 2px solid #dedede;
  cursor: pointer;
}

.status:hover {
  background-color: #f5f5f5;
}
